body {
    margin: 0;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 18px;
    color: #fff;
    -webkit-font-smoothing: antialiased;
    background-color: #fff;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  p {
    margin: 0 0 10;
  }
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
  }
  a{
      text-decoration: none;
      color: #fff;
  }
  .bigtitle{
    font-weight: 400;
    font-size: 80px;
    line-height: 80px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .btn--yellow{
    background: linear-gradient(182.88deg, #FFB548 11.27%, #F3AF34 90.4%);
    border-radius: 4px;
    padding:20px 22px ;
    border: none;
    cursor:pointer;
    outline:none;

    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 22px;
    /* identical to box height */

    text-align: center;

    color: #1A1917;

    &:hover{
        background: rgb(255, 183, 49);
    }
    
  }

  .header{
      align-items: center;
    position: absolute;
    width: 100%;
    z-index: 2;
    &__inner{
        padding: 20px;
        display: flex;
        align-items: center;
    }
    &__logo{
        width: 55px;
        height: 55px;
        margin-left: 20px;
    }
    .nav{
        display: flex;
        margin-left: 230px;
        &__menu a{
            color: #fff;
            text-transform: uppercase;
            transition: color .2s linear;
            +a{
                margin-left: 38px;
            }
            &:hover{
                color: #FFB548;
            }
        }
        &__select{
            margin-left: 170px;
            .is-active .nav__select-body{
                display: block;
            }
            &-header{
                display: flex;
                position: relative;
                cursor:pointer;
                color: #fff;
                transition: color .2s linear;
                &:hover{
                    color: #FFB548;
                }

            }
            &-body{}
            &-icon{
                position: absolute;
                left: 25px;
                bottom: 4px;
                img{
                }
            }
            &-current{
            }
            &-item{
                color: #fff;
            }
            &-body{
                display: none;
                background-color: black;
                border-radius: 4px;
                padding:5px 20px 5px 5px;
                color: #fff;
                transition: color .2s linear;
                cursor: pointer;
                &:hover{
                    color: #FFB548;
                }
                
            }
            &-item a{
                transition: color .2s linear;
                &:hover{
                    color: #FFB548;
                }
            }
        }
    }
    &__icons{
        position: relative;
        margin-left: 35px;
        border-left: 1px solid white;
        height:30px;
        &-inner{
            padding-left: 24px;
        }
        &-xbox{
            position: absolute;
            cursor: pointer;
            width: 30px;
            height: 27px;
            left: 24px;
        }
        &-steam{
            margin-left: 13px;
            position: absolute;
            left: 60px;
            width: 30px;
            height: 27px;
            cursor: pointer;
        }
    }
}


