.features{
    background-image: url('../img/features-bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 960px;
    &__content{
        float: right;
        max-width: 400px;
        margin-top: 167px;   
    }
    &__subtitle{
        margin-bottom: 10px;
    }
    &__title{
        margin-bottom: 70px;
    }
}
.accordeon{

    &-item{

    }
    &-1{

    }
    &-title{        
        font-family: Bebas Neue;
        position: relative;
        font-size: 36px;
        line-height: 36px;
        letter-spacing: 2px;
        color: rgb(255, 194, 103);
        display: block;
        cursor: pointer;
        padding: 5px;
        transition: color .2s linear;
        &:hover{
            color: rgb(255, 166, 0);
        }
        &:before{
            content:"";
            border: 1px solid white;
            border-radius: 50%;
            top: 50%;
            left: -15px;
            transform: translateY(-50%);
            position: absolute;
            background: transparent;
            height: 10px;
            width: 10px;
        }
    }
    
    &-text{
        display: none;
        padding: 5px;
        max-width: 363px;
    }
}
.accordeon-input{
    -webkit-appearance: none;
    appearance: none;
    width: 0;
    height: 0;
    position: absolute;
    &:checked~.accordeon-title{
        color: rgb(255, 166, 0);
        text-shadow: 1px 1px 2px black, 0 0 1em yellow;
    }
    &:checked~.accordeon-title:before{
        background: white;
    }
    &:hover~.accordeon-title:before{
        background: white;
    }
    &:checked~.accordeon-text{
        display: block;
    }
}

.system{
    background: url(../img/req-bg.png);
    background-repeat:no-repeat;
    background-position: center;
    background-size: cover;
    &__content{
        padding-top: 145px;
        margin: 0 auto;
        text-align: center;
        &-subtitle{
            margin-bottom: 10px;
        }
    }
    &__requirements{
        margin-top: 135px;
        padding-bottom: 170px;
    }
}

.quotes{
    /* background: url('../img/system-bg2.png') */
    background: black;
    &__inner{
        display: flex;
        justify-content: space-between;
        &-content{
            margin: 303px 0 269px 117px;
            .quotes-subtitle{
                margin-bottom: 10px;
            }
            .quotes-title{
                margin-bottom: 35px;
            }
            .quotes-text{
                max-width: 434px;

            }
            .quotes-button{
                margin-top: 35px;
                width: 260px;
                height: 60px;
            }
        }
        &-comment{
            margin-top: 160px;
            max-width: 700px;
            margin-right: 90px;
        }
    }
    .quotes__card{
        position: relative;
        margin-bottom: 30px;
        width: 472px;         
        background: rgba(255, 255, 255, 0.08);
        border-radius: 10px;
        &-inner{
            padding: 15px 20px 20px 20px;
            .comment-date{
                font-family: Open Sans;
                font-weight: normal;
                font-size: 14px;
                line-height: 19px;
    
                color: #FFFFFF;
    
                mix-blend-mode: normal;
                opacity: 0.5;
            }
            .comment__text{
                margin-bottom: 8px;
                font-family: Open Sans;
                font-weight: normal;
                font-size: 18px;
                line-height: 25px;
    
                color: #ffffff;
                max-width: 389px;
            }
        }
    }
    &-comment{
        &-header{
            display: flex;
            align-items: center;
            margin-bottom: 16px;
            position: relative;
            .quotes__user-photo{
                width: 67px;
                height: 66px;
                border-radius: 50%;
                overflow: hidden;
                margin-right: 22px;
                margin-bottom: 5px;
            }
            .twitter-logo{
                position: absolute;
                top: 9px;
                right: 5px;
            }
        }
        
        
    }
    &__user-about{
        .userName{
            font-size: 24px;
            line-height: 24px;
            letter-spacing: 2px;
            color: #FFB548;
        }
        .userProf{
            margin-top: 5px;
            font-family: Open Sans;
            font-weight: normal;
            font-size: 18px;
            line-height: 22px;
            color: #FFFFFF;

            opacity: 0.5;
        }
    }
}
.quotes__card-2{
    right: 130px;
}
.quotes__card-3{
    right: 30px;
}

